.input-group-dropdown {
  flex-wrap: nowrap;
}

.input-group-dropdown .select__control {
  border-top-left-radius: 0%;
  border-bottom-left-radius: 0%;
}

[dir="ltr"] .demo-inline-spacing > * {
  margin-right: inherit;
}

.rst__rowToolbar {
  display: inline-block !important;
}

.rst__moveHandle,
.rst__loadingHandle {
  z-index: inherit !important;
}

.css-1xtdfmb-LoadingDot {
  background-color: #212121 !important;
}
.css-zoievk-LoadingDot {
  background-color: #212121 !important;
}
.css-x748d8-LoadingDot {
  background-color: #212121 !important;
}

.dark-layout {
  .css-1xtdfmb-LoadingDot {
    background-color: #949494 !important;
  }
  .css-zoievk-LoadingDot {
    background-color: #949494 !important;
  }
  .css-x748d8-LoadingDot {
    background-color: #949494 !important;
  }
}

.no-data-grid {
  filter: grayscale(0.6);
  opacity: 0.5;
}

@media (max-width: 850px) {
  .no-data-grid {
    filter: grayscale(0.6) !important;
    opacity: 0.5 !important;
    width: 80% !important;
  }
}

input[name="select-row-"] {
  display: none !important;
}

.avatar-stats-shimmer {
  background-color: #ebebeb3b !important;
}
.dark-layout {
  .avatar-stats-shimmer {
    background-color: #efefef08 !important;
  }
}

.border-secondary2 {
  border: 1px solid #d8d6de !important;
}

.dark-layout .border-secondary2 {
  border: 1px solid #404656 !important;
}

.form-repeater-item {
  background-color: #f9f9f9;
}

.dark-layout .form-repeater-item {
  background-color: #161d31;
}

.dark-layout .swal2-popup {
  background: #161d31;
}

.selectable-input {
  .overflow-hidden {
    margin-bottom: 0 !important;
  }
  header {
    padding-left: 0 !important;
    padding-right: 0 !important;
    background-color: transparent !important;
  }
  .invoice-list-table-header {
    margin-left: 0 !important;
    margin-right: 0 !important;
  }
  .invoice-list-table-header .row .justify-content-xl-end {
    margin-left: 0 !important;
    margin-right: 0 !important;
    div.d-flex {
      margin-right: 0 !important;
    }
  }
  .react-dataTable .rdt_TableBody .rdt_TableRow .rdt_TableCell {
    height: 40px !important;
    padding: 0.5rem 0.5rem !important;
  }
}

.btn-group > .btn-check:checked + .btn,
.btn-group > .btn-check:focus + .btn,
.btn-group > .btn:hover,
.btn-group > .btn:focus,
.btn-group > .btn:active,
.btn-group > .btn.active,
.btn-group-vertical > .btn-check:checked + .btn,
.btn-group-vertical > .btn-check:focus + .btn,
.btn-group-vertical > .btn:hover,
.btn-group-vertical > .btn:focus,
.btn-group-vertical > .btn:active,
.btn-group-vertical > .btn.active {
  z-index: 0;
}

.apexcharts-toolbar {
  z-index: 0;
}

.dark-layout .card .gantt-tooltip {
  background-color: #111111 !important;
}

