.react-select-as-table {
  cursor: pointer !important;
  flex: auto !important;
  max-width: 100% !important;
  width: min-content !important;
  .select__control {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .select__indicators {
    display: none;
  }
