$row-padding: 10px;
$hover-padding: 10px;
.rst__rowWrapper {
  padding: $row-padding $row-padding $row-padding 0 !important;
  height: 100%;
  box-sizing: border-box;
  // cursor: move;

  &:hover {
    opacity: 1 !important;
    padding: $hover-padding $hover-padding $hover-padding 0;
  }

  &:active {
    opacity: 0.7 !important;
  }
}

.timeline-event {
  padding: 10px;
  cursor: pointer;
  border-radius: 10px;
}

.timeline-event:hover {
  background-color: #2f3a56;
  cursor: pointer;
  border-radius: 10px;
  color: white;
}

