.datatable-table-tree {
  .rdt_TableBody {
    .rdt_TableRow {
      .kUKaPk {
        .kHMizu {
          background-color: #f4faff !important;
        }
      }
    }
    .rdt_TableRow:has(.dt-disabled-expand) {
      .kUKaPk {
        .kHMizu {
          color: transparent;
          background-color: #f4faff !important;
          cursor: default;
          user-select: none;
        }
      }
    }
    .rdt_TableRow {
      .kUKaPk {
        min-width: 30px;
        flex: 0 0 30px;
      }
      .kHMizu[disabled] {
        color: transparent;
        background-color: #f4faff !important;
        cursor: default;
        user-select: none;
      }
    }
    .rdt_TableCell {
      height: 43px !important;
      padding: 0.72rem 1rem !important;
    }
    .rdt_ExpanderRow {
      padding-left: 24px;
    }
  }

  .gcyBe {
    background-color: inherit !important;
  }
}

